let PageComponent = class PageComponent {
    constructor(service, firestore, toastr) {
        this.service = service;
        this.firestore = firestore;
        this.toastr = toastr;
    }
    ngOnInit() {
        this.resetForm();
    }
    resetForm(form) {
        if (form != null)
            form.resetForm();
        this.service.formData = {
            id: null,
            pageName: '',
            pageTitle: '',
            pageDescription: '',
        };
    }
    onSubmit(form) {
        let data = Object.assign({}, form.value);
        delete data.id;
        if (form.value.id == null) {
            this.firestore.collection('pages').add(data);
            this.toastr.success("Page Created Successfully!!!", "ANGULAR CMS");
        }
        else {
            this.firestore.doc('pages/' + form.value.id).update(data);
            this.toastr.success("Page Updated Successfully!!!", "ANGULAR CMS");
        }
        this.resetForm(form);
    }
};
